import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Layout, Menu, Avatar,Typography } from 'antd';
import { UserOutlined } from '@ant-design/icons';
const { Header } = Layout;
const { SubMenu } = Menu;
const { Text  } = Typography;
class AppHeader extends Component {
    render() {
        return (
            <Header>
                <Menu theme="light" mode="horizontal" defaultSelectedKeys={['0']}>
                    <Menu.Item key="0"><Link to="/">Home</Link></Menu.Item>
                    {!this.props.authenticated && <Menu.Item style={{ 'float': 'right' }} key="3"><Link to="/login">Login</Link></Menu.Item>}
                    {!this.props.authenticated && <Menu.Item style={{ 'float': 'right' }} key="4"><Link to="/signup">Signup</Link></Menu.Item>}
                    {this.props.authenticated &&

                        <SubMenu style={{ 'float': 'right' }}
                            title={
                                <>
                                    {this.props.loggedInUser.imageUrl && <Avatar src={this.props.loggedInUser.imageUrl} />}
                                    {!this.props.loggedInUser.imageUrl && <Avatar icon={<UserOutlined />} />}
                                    <Text level={4}>{this.props.loggedInUser.name}</Text>
                                </>
                            }
                        >
                            <Menu.Item key="1"><Link to="/admin">Admin Dashboard</Link></Menu.Item>
                            <Menu.Item key="2"><Link to="/profile">Profile</Link></Menu.Item>
                            <Menu.Item key="3"><span onClick={this.props.onLogout}>Logout</span></Menu.Item>
                        </SubMenu>
                    }
                </Menu>
            </Header>
        )
    }
}

export default AppHeader;