import React from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { Card, Layout, Row, Col } from 'antd';
import { API_ROOT } from '../api-config';

const { Content } = Layout;
const { Meta } = Card;
export default class Home extends React.Component {
    constructor() {
        super();
        this.state = {
            cards: [],
        }
    }

    componentDidMount() {
        const config = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        };
        axios.get(`${API_ROOT}/topics`, config)
            .then(res => {
                const cards = res.data;
                this.setState({ cards });
            })
    }

    render() {
        return (
            <Content style={{ padding: '50px' }}>
                <Row gutter={[24, 24]}>
                    {this.state.cards.map((item, index) => (
                        <Col key={index} className="gutter-row" xs={12} sm={8} md={4}>
                            <Card
                                hoverable
                                style={{ textAlign: "center" }}
                                cover={<Link to={`/categories/${item.name}`}><img alt={item.name} src={item.imageUrl} style={{ height: "100px", width: "auto", margin: "24px auto 0" }} /></Link>}
                            >
                                <Meta title={<Link to={`/categories/${item.name}`}>{item.name}</Link>} style={{ textAlign: "center" }} />
                            </Card>
                        </Col>
                    ))}
                </Row>
            </Content>
        )
    }
}